<template>
  <div class="canvas-container">
    <div class="padding-wrapper"> <!--  padding -->
      <v-canvas/>
      <!--<cavans-area -->
      <!--v-if="canvasReady"-->
      <!--:grid-status="gridStatus"-->
      <!--:sub-action="subAction"-->
      <!--:sub-state="subState"-->
      <!--_v-e33f443c="">-->
      <!--</cavans-area>-->
    </div>
    <side-menu/>
    <scaler/>
  </div>
</template>

<script>
  /*
    initscroll-> nexttick-> canvasReady  canvas  -> refresh scroll
   */
  import IScroll from 'iscroll/build/iscroll-probe';
  import Scaler from '../components/Scaler';
  import SideMenu from './SideMenu';
  import { mapGetters } from 'vuex';
  import * as T from '../store/types';
  import vCanvas from './Canvas';
  import * as EvTypes from '../constant/events';

  export default {
    components: {
      vCanvas,
      SideMenu,
      Scaler,
    },
    methods: {
      initScroll () {
        window.myScroll = this.iscroll = new IScroll(this.$el, {
          disableMouse: true, // 
          mouseWheel: true, // 
          scrollbars: true, // 
          fadeScrollbars: false, //  fade 
          freeScroll: false,
          probeType: 3, // scrollrequestAnimationFrame
          scrollX: true, // 
          interactiveScrollbars: true, // 
        });
        this.iscroll.on('scrollEnd', () => {
          this.eventHub.$emit(EvTypes.CANVAS_SCROLL_END);
        });
      },
    },
    mounted () {
      this.initScroll();
      /*
       layout next next 
       //            this.initScroll();
       //            this.canvasReady = true; // 
       //            this.$nextTick(()=> {
       //              window._iscroll.refresh(); // 
       //              resolve();
       //            });
       */
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .canvas-container {
    background: #efefef;
    position: absolute;
    left: 360px;
    top: 50px;
    right: 300px;
    bottom: 0;
    padding: 30px 0 150px 0;
    overflow: hidden;

    .padding-wrapper {
      padding-bottom: 400px;
    }
    .iScrollHorizontalScrollbar,
    .iScrollVerticalScrollbar {
      z-index: 999 !important;
    }
  }
</style>



