/*
 preChooseObj
 
 preChooseObj 
 () preChooseObj 

  (subscribe) 

 // TODO addingway 
 //  isClickAdding isDragAdding subscribe  mutation

 */
import * as T from '../types';

const plugin = (store) => {
  store.subscribe((mutation) => {
    if (mutation.type === T.ADD_ELEMENT) {
      console.log(`>>> subscribe${T.ADD_ELEMENT}`);
      const payload = mutation.payload;
      const { model, addingWay } = payload;
      const { type: modelType, id } = model;
      if (addingWay === 'click' && modelType === 'text') {
        // 
        const preChooseObj = store.getters[T.GET_PRE_CHOOSE_OBJ];
        if (preChooseObj) {
          console.log(`to delete preChooseObj ${preChooseObj}`);
          const elementId = preChooseObj.elementId;
          // if (typeof elementId === 'object') {
          //   const groupId = this.getMotherGroupId(elementId[0]);
          //   const ids = elementId.concat(groupId);
          //   this.actions.deleteElementList(ids);
          // } else {
          store.commit(T.RM_ELEMS_BY_ID, {
            ids: [elementId],
          });
          // }
        }
        store.commit('SET_PRE_CHOOSE_OBJ', {
          elementId: id,
          type: `${modelType}-item`,
        });
      }
    }
  });
};
export default plugin;



