<template>
  <div id="app">
    <!--<div>{{name}}</div>-->
    <!--<test :name="name"></test>-->
    <h1 v-if="!loaded" align="center"></h1>
    <top-header v-if="loaded"></top-header>
    <editor v-if="loaded"></editor>
  </div>
</template>

<script>
  import store from './store';
  import * as T from './store/types';
  import TopHeader from './components/TopHeader';
  import Editor from './components/Editor';
  import DEFAULT_PAGE_DATA from './constant/default-page-data';

  export default {
    name: 'app',
    store,
    components: {
      TopHeader,
      Editor,
    },
    data () {
      return {
        name: 'peter',
        loaded: false,
        canvasReady: false,
      }
    },
    methods: {
      fetchPageData () {
        //  url  id 
        return new Promise((resolve)=> {
          setTimeout(function () {
            resolve(DEFAULT_PAGE_DATA);
          }, 1000);
        });
      },
      init () {
        this.$store.dispatch(T.INIT_DATA_FROM_URL)//
          .then(()=> {
            this.loaded = true; // 
          });

//        this.fetchPageData()
//          .then(this.convertPageData) // 
//          .then(this.setInitData) // 
//          .then(this.prepareCanvas); // 
        // TODO 
      },
    },
    created () {
      console.log('>>> <App> created');
      // 
      this.init();
    },
    mounted () {
      $('.editor-header')
        .on('move', function (e) {
          console.log('move')
          // move .mydiv horizontally
//          jQuery(this).css({ left: e.startX + e.deltaX });
        })
    }
  };
</script>

<style>
  @keyframes rotate {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .drag-add {
    position: relative;
    user-select: none;
    cursor: pointer;
    overflow: hidden;
  }

  body {
    font-family: sans-serif, Microsoft YaHei, STHeitiSC-Light;
    -webkit-font-smoothing: antialiased;
    background: #fff;
    margin: 0;
  }

  .btn {
    display: inline-block;
    margin: auto 12px;
    height: 34px;
    line-height: 34px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }

  .spinning {
    animation: rotate 0.8s linear infinite;
  }

</style>



