import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import * as getters from './getters';
import mutations from './mutations';
import preChooseObj from './subscibe/preChooseObj';
import autoSelect from './subscibe/autoSelect';
// import createLogger from '../../../src/plugins/logger';

Vue.use(Vuex);

// const debug = process.env.NODE_ENV !== 'production'
const state = {
  background: {},
  elemIdArr: [],
  elems: {},
  canvasSize: {},
  //---------------
  isFetching: true,
  editingElementId: null,
  scale: 0.5,
  nextId: 1, //  1  0 
  dragObj: null,
  selectBox: null,
  preChooseObj: null,
  //-----------------
  controlPannelType: 'bg', //  'bg'
  selectedElemId: null, // 
  //---------------
  isElemMoving: null, // 
  attractMovingInfo: {
    left: null,
    top: null,
  }, // 
  //-------------
  picLibUploading: false, // 
  isGridLineVisible: false, // 
  //----
  meta: null, //  ... ID 
  ownId: null, //  ID  ID
  folkId: null, //  ID
  //---
  previewVisible: false, // 
};

export default new Vuex.Store({
  // strict: process.env.NODE_ENV !== 'production',
  plugins: [preChooseObj, autoSelect],
  state,
  mutations,
  actions,
  getters,
  modules: {},
  // strict: debug,
  // plugins: debug ? [createLogger()] : []
});



